.menu-aplicacion {
    background-color: blueviolet;
    height: 40px;
    padding: 0 7px;
}

.menu-aplicacion .cont-menu {
    height: 40px;
    display: flex;
    justify-content: flex-end;
    align-items: center;   
}

.menu-aplicacion .cont-menu a {
    color: white;
   /*  background-color: cadetblue; */
    margin-left: 10px;
    font-size: 20px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-transform: uppercase;
    font-weight: 300;
}

